 
import axios from "axios";

import constant from "./constant"

import { Notification, MessageBox, Message } from "element-ui";

// 第三方插件
import "element-ui/lib/theme-chalk/index.css";

 
import router from "../router";
 

const service = axios.create({
	baseURL: constant.BASE_URL,
	timeout: 50000
});

service.defaults.withCredentials = false; // 让ajax携带cookie
service.interceptors.request.use(

	
	// 每次请求都自动携带Cookie
	config => {
		config.headers.cxygzl = localStorage.getItem("cxygzl")
 
		return config;
	},
	// eslint-disable-next-line handle-callback-err
	error => {
		return Promise.reject(error);
	}
);

service.interceptors.response.use(
	rsp => {

		return rsp;
	},
	// 拦截异常的响应
	err => {
		console.log("请求", err);
		switch (err.response.status) {
			case 401:
				MessageBox.alert("登陆已过期，请关闭当前窗口重新进入-登录页面","",{
				 
					callback:function(a,b){
						if(a==='confirm'){
							router.push("/")
						}
					}
				});
				break;
			
			case 400:
				Message.error(err.response.data)
				break;
			case 403:
				//Message.warning("抱歉，您无权访问！")
				break;
			case 500:
				Notification.error({ title: "提醒", message: "服务器出了点小错误" });
				break;
			case 404:
				Notification({
					title: "提醒",
					message: "未找到，检查参数",
					type: "warning"
				});
				break;
			default:
				//throw 'request error'
				return Promise.reject(err);
		}
		//throw 'request error'
		return Promise.reject(err);
	}
);

export default service;
