import request from '@/api/request.js'


// 登录
export function login(param) {
  return request({
    url: '/user/login',
    method: 'post',
    data: param
  })
}

// 获取当前登录用户信息
export function getCurrentUserDetail(param) {
  return request({
    url: 'user/getCurrentUserDetail',
    method: 'post',
    data: param
  })
}

// 创建用户
export function createUser(param) {
  return request({
    url: 'user/createUser',
    method: 'post',
    data: param
  })
}
// 修改用户信息
export function editUser(param) {
  return request({
    url: 'user/editUser',
    method: 'post',
    data: param
  })
}

// 获取用户详细信息
export function getUserDetail(param) {
  return request({
    url: 'user/getUserDetail',
    method: 'get',
    params: param
  })
}


// 查询用户列表
export function queryUserList(param) {
  return request({
    url: 'user/queryUserList',
    method: 'post',
    data: param
  })
}
 
// 离职
export function leave(param) {
  return request({
    url: 'user/leave',
    method: 'post',
    data: param
  })
}

export default {
  login,getCurrentUserDetail,createUser,editUser,getUserDetail,queryUserList,leave
}
