<template>
	<div id="app">
		<template v-if="url == '/login' || url == '/'">
			<transition name="router-fade" mode="out-in">
				<router-view v-if="!$route.meta.keepAlive" />
			</transition>
		</template>
		<template v-else>
			<el-container>
				<el-header class="global-header">


					<div style="height: 60px;background-color: #545c64;">

						<div class="header-div1">

								<div class="title">
									CXYGZL
								</div>

						</div>
						<div class="header-div2">

							<el-avatar  v-if="userInfo" shape="square" :size="50" :src="userInfo.avatarUrl"></el-avatar>






						</div>
						<div class="header-div3">
							<el-dropdown v-if="userInfo" @command="clickHeaderDropDown">
								<span class="el-dropdown-link" style="color: white;">
									{{userInfo.name}}
								</span>
								<el-dropdown-menu slot="dropdown">
									<el-dropdown-item command="exit">退出</el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>
						</div>


					</div>



				</el-header>
				<el-container>
					<el-aside width="200px" class="global-left">

						<el-menu :default-active="activeId" class="el-menu-vertical-demo" background-color="#545c64"
							text-color="#fff" active-text-color="#ffd04b">

							<template v-for="(item, index) in menuList">

								<el-submenu :index="item.id" v-if="item.children.length > 0">
									<template slot="title">
										<i :class="item.icon"></i>
										<span>{{ item.title }}</span>
									</template>

									<template v-if="item.children.length > 0">
										<el-menu-item v-for="(item1, index1) in item.children" :index="item1.id"
											@click="openMenu(item1.url)">
											<template slot="title">{{ item1.title }}</template>
										</el-menu-item>
									</template>

								</el-submenu>
								<el-menu-item v-else :index="item.id" @click="openMenu(item.url)">
									<i :class="item.icon"></i>
									<span slot="title">{{ item.title }}</span>
								</el-menu-item>

							</template>



						</el-menu>





					</el-aside>
					<el-container>
						<el-main style="padding: 0px;" class="global-main">
							<transition name="router-fade" mode="out-in">
								<router-view v-if="!$route.meta.keepAlive" />
							</transition>

						</el-main>
					</el-container>
				</el-container>
			</el-container>

		</template>

	</div>
</template>

<script>

import { getCurrentUserDetail } from '@/api/user'

export default {
	components: {},
	data() {
		return {
			userInfo: undefined,
			url: '',
			activeId: '1-1',
			menuList: [
				{
					title: '组织架构',
					id: "1",
					icon: 'el-icon-location',
					children: [

						{
							id: "1-1",

							title: '用户管理',
							url: '/org/user'
						},
						{
							id: "1-2",

							title: '部门管理',
							url: '/org/dept'
						}
					]
				},
				{
					id: "2",
					title: '流程面板',
					url: '/formsPanel',
					icon: 'el-icon-s-tools',
					children: []
				},
				{
					id: "3",
					title: '我的流程',
					icon: 'el-icon-s-order',
					url: '/workSpace',
					children: []
				}
			]
		}
	},
	mounted() {
		let beforeUrl = sessionStorage.getItem('router-path')
		if (beforeUrl === undefined || beforeUrl === '') {
			//this.$router.push("/workPanel")
		} else {
			//this.$router.push(beforeUrl)
		}

	},
	watch: {
		//监听路由地址的改变
		$route: {
			immediate: true,
			handler() {
				this.url = this.$route.path;
				if (this.url === '/' || this.url === '/login') {

				} else if (!this.userInfo) {
					getCurrentUserDetail().then(res => {
						this.userInfo = res.data
						this.$store.commit('loadLoginUser', res.data)
					})
				}

				var menuList = this.menuList;

				for (var item of menuList) {
					if (item.url === this.url) {
						this.activeId = item.id
						break
					} else {
						var children = item.children;

						for (var item1 of children) {
							if (item1.url === this.url) {
								this.activeId = item1.id
								break
							}
						}

					}
				}

			}
		}
	},
	methods: {
		openMenu(url) {
			if (url && url.length > 0) {

				this.$router.push(url)
			}
		},
		clickHeaderDropDown(e){
			console.log(e)
			if(e==='exit'){
				this.$router.push("/")
			}
		}
	}
}
</script>
<style lang="less">
@import "@/assets/global";

:focus {
	outline: -webkit-focus-ring-color auto 0px;
}

body {
	margin: 0;
	min-width: 500px;
	background-color: #f5f6f6;
}

body,
html {
	margin: 0;
	height: 100%;
}

ul {
	padding: 0;
	margin: 0;

	li {
		list-style-type: none;
	}
}

.ov-tip {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
}

.item-desc {
	font-size: small;
	color: #5e5e5e;
}

.max-fill {
	width: 100% !important;
}

.global-header {
	padding: 0px !important;
}

@variable_header_height: 60px;

.global-left {
	height: calc(100vh - @variable_header_height);
	background-color: rgb(84, 92, 100);
}

.global-main {
	height: calc(100vh - @variable_header_height);

}

@variable_header_right1_width: 60px;

@variable_header_right2_width: 200px;

.header-div1 {
	float: left;

	width: calc(100vw - @variable_header_right1_width - @variable_header_right2_width);

	.title{
		height: @variable_header_height;
	line-height: @variable_header_height;
	font-size: 30px;
	font-weight: bold;
	color: white;
	padding-left: 30px;
	}
}

.header-div2 {
	float: left;
	width: @variable_header_right1_width;
	height: @variable_header_height;
	padding-top: 5px;
}

.header-div3 {
	float: left;
	cursor: pointer;
	width: @variable_header_right2_width;
	height: @variable_header_height;
	padding-top: 20px;
}
</style>
