import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

const viewport = {
  content: "width=device-width, initial-scale=1.0, user-scalable=no"
}

const router = new Router({
  //mode: 'history',
  //base: __dirname,
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
      path: "/login",
      name: "login",
      component: () => import("@/views/login.vue"),
      meta: {title: '登录', viewport: viewport}
    },
    {
      path: "/index",
      name: "index",
      component: () => import("@/views/Index.vue"),
      meta: {title: 'OA工作流', viewport: viewport}
    },
    {
      path: "/workspace",
      name: "workspace",
      component: () => import("@/views/workspace/WorkSpace.vue"),
      meta: {title: '我的流程', viewport: viewport}
    },
    {
      path: "/formsPanel",
      name: "formsPanel",
      component: () => import("@/views/admin/FormsPanel.vue"),
      meta: {title: '流程面板', viewport: viewport}
    },
    {
      path: "/org/dept",
      name: "orgdept",
      component: () => import("@/views/org/dept.vue"),
      meta: {title: '部门管理', viewport: viewport}
    },
    {
      path: "/org/user/create",
      name: "orgUserCreate",
      component: () => import("@/views/org/user/create.vue"),
      meta: {title: '添加用户', viewport: viewport}
    },
    {
    path: "/org/user/edit",
    name: "orgUserEdit",
    component: () => import("@/views/org/user/edit.vue"),
    meta: {title: '编辑用户', viewport: viewport}
  },
    {
      path: "/org/user",
      name: "orgUser",
      component: () => import("@/views/org/user.vue"),
      meta: {title: '用户管理', viewport: viewport}
    },

    {
      path: "/admin/design",
      name: "design",
      component: () => import("@/views/admin/FormProcessDesign.vue"),
      meta: {title: '表单流程设计', viewport: viewport}
   /*   children: [
        {
          path: "baseSetting",
          name: "baseSetting",
          component: () => import("@/views/admin/layout/FormBaseSetting.vue"),
          meta: {title: '基础设置'}
        }, {
          path: "formSetting",
          name: "formSetting",
          component: () => import("@/views/admin/layout/FormDesign.vue"),
          meta: {title: '表单设计'}
        }, {
          path: "processDesign",
          name: "processDesign",
          component: () => import("@/views/admin/layout/ProcessDesign.vue"),
          meta: {title: '流程设计'}
        }, {
          path: "proSetting",
          name: "proSetting",
          component: () => import("@/views/admin/layout/FormProSetting.vue"),
          meta: {title: '高级设置'}
        }
      ]*/
    }
  ]
})

const originalReplace = Router.prototype.replace;
Router.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch((err) => err);
};
let originPush = Router.prototype.push
// 重写push方法
Router.prototype.push = function (location,resolve,reject){
  if (resolve && reject){   //如果传入了resolve和reject两个回调，则正常执行原来的push方法
    originPush.call(this,location,resolve,reject)
  }else{   // 否则就执行指定的方法
    originPush.call(this,location,($router)=>{},(err)=>{

    })
  }
}

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  if (to.meta.content) {
    let head = document.getElementByTagName('head')
    let meta = document.createElemnet('meta')
    meta.name = 'viewport'
    meta.content = "width=device-width, initial-scale=1.0, user-scalable=no"
    head[0].appendChild(meta)
  }

  next();
  sessionStorage.setItem('router-path', to.path)
})

export default router;
